            <div
              class="indicator_price"
              :class="[isPositive ? 'green' : 'red']"
            >
              <span>{{ balance }}</span>
              &#8381;
            </div>
          </div>
        </div>
        <ul class="app-data_view-list flex flex-col">
          <li class="view-list_Item view-list-titles flex">
            <h3 class="view-list_Item-element view-list-titles_Item">
              Hазвание расходов
            </h3>
            <h3 class="view-list_Item-element view-list-titles_Item">
              Стоимость
            </h3>
            <h3 class="view-list_Item-element view-list-titles_Item"></h3>
          </li>
          <InfoExpence
            v-for="(item, idx) in infos"
            :key="idx"
            :productName="item.myProduct"
            :productPrice="item.myPrice"
          >
            <button
              @click="editInfo(idx)"
              class="btn view-list_Item-controls_btn btn--edit"
            ></button>
            <button
              @click="deleteInfo(idx)"
              class="btn view-list_Item-controls_btn btn--delete"
            ></button>
          </InfoExpence>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import InfoExpence from "./components/InfoExpense.vue";

export default {
  name: "App",
  components: {
    InfoExpence,
  },

  data() {
    return {
      inputs: {
        palceholder: {
          budget: "Введите ваш бюджет",
          product: "Введите название покупки",
          price: "Введите цену покупки",
          error: "Поле не должно быть пустым",
        },
        isEmpty: false,
        input: {
          budget: null,
          product: "",
          price: null,
        },
        value: {
          myBudget: 0,
        },
      },
      isPositive: false,
      infos: [],
    };
  },

  mounted() {
    if (localStorage.length !== 0) {
      this.inputs.value.myBudget = JSON.parse(localStorage.getItem("budget"));
      this.infos = JSON.parse(localStorage.getItem("infos"));
    } else {
      return
    }
  },

  watch: {},

  methods: {
    numValid($event) {
      let keyCode = $event.keyCode ? $event.keyCode : $event.which;
      if (keyCode < 48 || keyCode > 57) {
        $event.preventDefault();
      }
    },
    addBudget() {
      if (this.inputs.input.budget !== null) {
        this.inputs.value.myBudget = this.inputs.input.budget;
        this.inputs.isEmpty = false;
      } else {
        this.inputs.isEmpty = true;
        this.inputs.value.myBudget = 0;
      }
      this.inputs.input.budget = null;
      this.colorBalance();
      this.addLS("budget", this.inputs.value.myBudget);
    },

    addLS(name, val) {
      localStorage.setItem(name, JSON.stringify(val));
    },

    createInfo() {
      let info = {
        myProduct: "",
        myPrice: null,
      };
      if (
        this.inputs.input.product !== "" &&
        this.inputs.input.price !== null
      ) {
        this.inputs.isEmpty = false;

        info.myProduct = this.inputs.input.product;
        info.myPrice = +this.inputs.input.price;
        this.infos.push(info);
      } else {
        this.inputs.isEmpty = true;
        return;
      }
      this.removeInputs();
      this.colorBalance();
      this.addLS("infos", this.infos);
    },

    deleteInfo(idx) {
      this.infos.splice(idx, 1);
      this.addLS("infos", this.infos);
    },

    editInfo(idx) {
      if (
        this.inputs.input.product === "" &&
        this.inputs.input.price === null
      ) {
        this.inputs.input.price = this.infos[idx].myPrice;
        this.inputs.input.product = this.infos[idx].myProduct;
        this.deleteInfo(idx);
      } else {
        return;
      }
      this.addLS("infos", this.infos);
    },

    removeInputs() {
      (this.inputs.input.product = ""), (this.inputs.input.price = null);
    },

    colorBalance() {
      this.isPositive = false;
      if (this.balance >= 0) {
        this.isPositive = true;
      }
    },
  },
  computed: {
    allExpence() {
      let totalExpence = [];

      for (let i = 0; i < this.infos.length; i++) {
        let itemInfos = this.infos[i];
        totalExpence.push(itemInfos.myPrice);
      }

      return totalExpence.reduce((acc, num) => acc + num, 0);
    },
    balance() {
      let balance = this.inputs.value.myBudget - this.allExpence;
      return balance;
    },
  },
};
</script>

<style lang="scss">
:root {
  --text: #212121;
  --app_bg: rgb(255, 255, 255);
  --green: rgb(35, 223, 35);
  --red: rgb(250, 33, 33);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
  font-size: 16px;
}

body {
  position: relative;
  max-width: 100%;
  width: 100%;
  font-family: "Poppins", sans-serif;
}

img {
  display: block;
  max-width: 100%;
  /* object-fit: contain; */
}

.btn {
  display: flex;
  font-family: inherit;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: none;
}

.flex {
  display: flex;
}

.flex-cc {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex-sc {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.flex-ec {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.flex-sb {
  justify-content: space-between;
}

.abs {
  position: absolute;
}

.flex-row {
  flex-flow: row nowrap;
}

.flex-col {
  flex-flow: column nowrap;
}

.hidden {
  opacity: 0;
}

.container {
  width: 90%;
  margin: 0 auto;
}

.app {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  line-height: 1.2;
  overflow: hidden;
  background-color: var(--app_bg);
  &_title {
    margin-top: 2rem;
  }
  &-wrapper {
    justify-content: space-between;
    max-height: 30rem;
  }
  &-data {
    flex: 0 0 35%;
    margin: auto 0;
    &_budget,
    &_expense {
      label {
        font-size: 1.3rem;
        margin-top: 1.5rem;
      }
      input {
        width: 100%;
        padding: 0.8rem 0.5rem;
        margin-top: 1rem;
        font-size: 1rem;
        color: inherit;
        border: 1px solid rgb(133, 133, 228);
        border-radius: 12px;
        outline: none;
        &:focus {
          box-shadow: 0 0 10px rgb(133, 133, 228);
        }
      }
      button {
        width: 40%;
        padding: 0.5rem;
        margin: 0 auto;
        margin-top: 1rem;
        border: 1px solid #918f8f;
        border-radius: 0.8rem;
        &:active {
          transform: translate(0.25rem, 0.25rem);
        }
      }
    }
    &_expense {
      margin-top: 3rem;
    }
  }
  &-data_view {
    flex: 0 0 48%;
    .indicator {
      justify-content: center;
      align-items: center;

      &_title {
        font-size: 1.5rem;
      }
      &_image {
        margin: 1.5rem 0;
        width: 4rem;
        height: 4rem;
        fill: blueviolet;
      }
      &_price {
        font-size: 3rem;
      }
    }
    &-list {
      margin-top: 1rem;
      .view-list_Item {
        align-items: center;
        justify-content: space-between;
        // margin-top: 1.5rem;
        &-element {
          flex: 0 0 33.3333%;
          text-align: center;
          max-height: 2rem;
        }
        .view-list_Item-controls {
          justify-content: flex-end;
          &_btn {
            position: relative;
            width: 2rem;
            height: 2rem;
            padding: 4px;
            margin-left: 0.5rem;
            &::before {
              position: absolute;
              display: block;
              content: "";
              width: 1.5rem;
              height: 1.5rem;
              background-position: center center;
              background-size: 100%;
              background-repeat: no-repeat;
            }
          }
          .btn--edit {
            &::before {
              background-image: url(./assets/images/edit.svg);
            }
          }
          .btn--delete {
            &::before {
              background-image: url(./assets/images/delete.svg);
            }
          }
        }
      }
    }
  }
}

.red {
  color: var(--red);
}

.green {
  color: var(--green);
}
</style>
